/* styles */
.wrapper {
	display: flex;
	flex-direction: column;
	min-height: 100vh;
	width: 100%;
	max-width: 100%;
	margin: 0 auto;
	overflow-x: hidden;
}

.header,
.footer {
	flex: 0 0 auto;
}

.main-page {
	flex: 1 0 auto;
	overflow: hidden;
}

.container {
	max-width: 1440px;
	width: 100%;
	padding: 0 10px;
	margin: 0 auto;
	color: $txt-color;
}

// No scrolling
.js-no-scroll {
	overflow: hidden;
}