.overlay {
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #000;
  opacity: 0;
  visibility: hidden;
  z-index: -1;
  position: fixed;
  width: 100%;
  height: 100%;
  transition: opacity 0.2s linear, visibility 0.2s linear;

  &.is-visible {
    opacity: 0.8;
    visibility: visible;
    z-index: 100;
  }
}

.main-page {
  background-image: url(../img/bg-space.png);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  position: relative;
  height: 100vh;
  /* Use vh as a fallback for browsers that do not support Custom Properties */
  height: calc(var(--vh, 1vh) * 100);
}

.main-holder {
  position: relative;

  display: flex;
  align-items: center;
  justify-content: center;

  height: 100%;
  width: 100%;

  @include max-screen(540px) {
    align-items: flex-end;
  }
}

.main-logo {
  position: absolute;
  @include posCenter(50%);
  z-index: 100;

  cursor: pointer;

  @include min-screen(1024px) {
    &:hover {
      z-index: 100;

      .main-logo__info {
        z-index: 10;
        opacity: 0.8;
        pointer-events: unset;
        transform: translate(-50%, 0);
      }
    }
  }

  @include max-screen(540px) {
    top: 50px;
    transform: translate(-50%, 0);
  }

  &__title {
    font-size: 16px;
    line-height: 18px;
    color: $white;

    margin: 0 0 10px;
  }


  &__wrap {}

  &__info {
    position: absolute;
    z-index: -1;
    bottom: 100%;
    left: 50%;
    transform: translate(-50%, 100%);

    min-width: 360px;

    padding: 16px 20px;

    font-size: 12px;
    line-height: 16px;
    font-weight: 500;
    color: $white;

    pointer-events: none;
    border-radius: 40px;
    opacity: 0;
    background: rgba(0, 0, 0, 0.9);
    box-shadow: 5px 5px 5px rgba(0, 0, 0, 0.5);
    backdrop-filter: blur(15px);

    transition: opacity 0.2s ease-in-out,
      transform 0.4s cubic-bezier(0.215, 0.610, 0.355, 1);

    p {
      margin: 0;
      text-align: center;
    }
  }

  &__decor {
    display: flex;
    position: absolute;
    z-index: -1;
    top: 50%;
    left: 50%;

    min-width: 240px;
    min-height: 240px;

    transform: translate(-50%, -50%);

    @include max-screen(540px) {
      min-width: 200px;
      min-height: 200px;
    }

    img {
      animation-name: orbit-reverse;
      animation-iteration-count: infinite;
      animation-timing-function: linear;
      animation-duration: 40s;
    }
  }

  &__img {

    display: flex;
    width: 120px;
    height: 120px;

    @include max-screen(1200px) {
      width: 100px;
      height: 100px;
    }
  }

  &__link {
    position: absolute;
    top: 0;
    left: 0;

    display: block;
    width: 100%;
    height: 100%;

    opacity: 0;

    font-size: 0.1px;

    @include max-screen(1024px) {
      display: none;
    }
  }
}

.list {
  position: relative;

  width: 500px;
  height: 500px;

  list-style: none;

  @include max-screen(1200px) {
    width: 450px;
    height: 450px;
  }

  @include max-screen(1024px) {
    width: 400px;
    height: 400px;
  }

  @include max-screen(540px) {
    display: flex;
    flex-wrap: wrap;

    width: 100%;
    height: auto;

    margin-bottom: 30px;
  }

  &__items {
    position: absolute;
    transform: translateY(0);

    cursor: pointer;

    will-change: transform;
    animation-name: moving;
    animation-iteration-count: infinite;
    animation-timing-function: linear;

    @include max-screen(540px) {
      position: static;
      margin: 6px 0;
      max-width: 33.3333%;
      width: 100%;
    }

    &:first-of-type {
      top: 0;
      left: 50%;
      transform: translate(-50%, -50%);

      @include max-screen(540px) {
        transform: translate(0);
      }
    }

    &:nth-of-type(2) {
      top: 0%;
      left: 10%;
    }

    &:nth-of-type(3) {
      top: 28%;
      left: -8%;
    }

    &:nth-of-type(4) {
      top: 62%;
      left: -6%;
    }

    &:nth-of-type(5) {
      bottom: -5%;
      left: 20%;
    }

    &:nth-of-type(6) {
      bottom: -5%;
      right: 20%;
    }

    &:nth-of-type(7) {
      bottom: 20%;
      right: -6%;
    }

    &:nth-of-type(8) {
      bottom: 54%;
      right: -8%;

    }

    &:nth-of-type(9) {
      bottom: 80%;
      right: 10%;
    }

    @include min-screen(1024px) {
      &:hover {
        z-index: 100;

        .list__info {
          z-index: 10;
          opacity: 0.8;
          pointer-events: unset;
          transform: translate(-50%, 0);
        }
      }
    }
  }

  &__title {
    font-size: 16px;
    line-height: 18px;
    color: $white;

    margin: 0 0 10px;
  }

  &__info {
    position: absolute;
    z-index: -1;
    bottom: 100%;
    left: 50%;
    transform: translate(-50%, 100%);

    min-width: 360px;

    padding: 16px 20px;

    font-size: 12px;
    line-height: 16px;
    font-weight: 500;
    color: $white;

    pointer-events: none;
    border-radius: 40px;
    opacity: 0;
    background: rgba(0, 0, 0, 0.9);
    box-shadow: 5px 5px 5px rgba(0, 0, 0, 0.5);
    backdrop-filter: blur(15px);

    transition: opacity 0.2s ease-in-out, transform 0.4s cubic-bezier(0.215, 0.610, 0.355, 1);

    p {
      margin: 0;
      text-align: center;
    }

    &._min-width {
      min-width: 460px;
    }
  }

  &__img {
    display: flex;
    align-items: center;
    justify-content: center;

    width: 100px;
    height: 100px;

    transform: scale(1);
    border: solid 2px #17161A;
    border-radius: 50%;
    box-shadow: 0 0 .1rem #fff,
      0 0 .2rem #fff,
      0 0 .3rem #fff,
      0 0 .8rem #fff,
      0 0 1rem #fff,
      inset 0 0 1.3rem #fff;

    animation-name: impulse;
    animation-iteration-count: infinite;
    animation-timing-function: linear;
    animation-direction: alternate;
    animation-duration: 1.5s;

    transition: transform 0.2s linear;

    @include max-screen(1200px) {
      width: 80px;
      height: 80px;
    }

    @include max-screen(540px) {
      margin: 10px auto;
      width: 90px;
      height: 90px;
    }

    @include max-screen(380px) {
      width: 80px;
      height: 80px;
    }

    &--bg-default {
      background-color: #fff;
    }

    &:not(.default-size) {

      img {
        width: 100%;
        height: 100%;
        border-radius: 50%;
      }
    }

    &:hover {
      transform: scale(1.1);
    }
  }

  &__link {
    position: absolute;
    top: 0;
    left: 0;

    display: block;
    width: 100%;
    height: 100%;

    opacity: 0;

    font-size: 0.1px;

    @include max-screen(1024px) {
      display: none;
    }
  }
}


// Descr modal
.descr-modal {
  position: absolute;
  @include posCenter(50%);
  z-index: -1;

  padding: 24px;

  max-width: 420px;
  width: 100%;

  opacity: 0;
  visibility: hidden;
  background: rgba(0, 0, 0, 0.7);
  box-shadow: 5px 5px 5px rgba(0, 0, 0, 0.5);
  border-radius: 10px;

  transition: opacity 0.2s linear,
    visibility 0.2s linear;

  &__close {
    position: absolute;
    top: 1px;
    right: 1px;

    width: 36px;
    height: 36px;

    border-width: 1px;
    border-style: solid;
    border-color: transparent;
    border-radius: 10px;

    background-color: transparent;
    opacity: 0.8;
    transform: rotate(0);

    transform-origin: center;
    transition: transform 0.2s ease, border-color 0.3s ease-in-out;

    &:after,
    &:before {
      content: '';
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%) rotate(45deg);

      background-color: $white;
      border-radius: 2px;
    }

    &:after {
      width: 30px;
      height: 3px;
    }

    &:before {
      width: 3px;
      height: 30px;
    }

    &:hover {
      opacity: 1;
      transform: rotate(360deg);
      border-color: $white;
    }
  }

  &__holder {
    margin-bottom: 40px;
  }

  &__title {
    font-size: 20px;
    line-height: 24px;
    font-weight: 700;
    color: $white;

    margin: 0 20px 16px;
  }

  &__wrapper {
    font-size: 14px;
    line-height: 20px;
    font-weight: 500;
    color: $white;

    margin-bottom: 16px;

    p {
      &:last-child {
        margin: 0;
      }
    }
  }

  &__caption {
    display: inline-block;
    margin-bottom: 8px;
    font-size: 14px;
    line-height: 20px;
    font-weight: 600;
    color: #9B84A3;
    text-align: left;
  }

  &__icon {
    display: flex;
    align-items: center;

    font-style: normal;
    text-align: left;

    border: 1px solid $white;
    border-radius: 10px;
  }

  &__link {
    padding: 8px 32px;
    display: block;
    width: 100%;
    font-size: 14px;
    line-height: 20px;
    font-weight: 700;
    color: $white;
    text-decoration: none;
    position: relative;
    opacity: 1;
    transition: opacity 0.2s ease-in-out;

    &:hover {
      opacity: 0.7;
      text-decoration: underline;

      &:after {
        transform: translate(10px, -50%);
      }
    }

    &:after {
      content: '';
      position: absolute;
      top: 50%;
      right: 32px;
      transform: translate(0, -50%);
      width: 21px;
      height: 16px;
      background: url(../../img/icon-arrow.svg) no-repeat center;
      background-size: contain;
      transition: transform 0.3s linear;
    }
  }

  &__logo {
    position: absolute;
    left: 50%;
    bottom: -44px;
    transform: translate(-50%, 0);
    display: flex;
    align-items: center;
    justify-content: center;
    width: 88px;
    height: 88px;
    background-color: $white;
    border-radius: 50%;
    box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.8);
    overflow: hidden;
  }

  &.is-open {
    opacity: 1;
    visibility: visible;
    z-index: 999;
  }
}

@keyframes impulse {

  /* максимальное свечение */
  100% {
    box-shadow:
      0 0 10px #fff,
      0 0 20px #fff,
      0 0 30px #fff,
      0 0 40px #fff,
  }

  /* минимальное свечение */
  0% {
    box-shadow:
      0 0 1px #fff,
  }
}


@keyframes moving {
  0% {
    transform: translateY(0);
  }

  100% {
    transform: translateY(10px);
  }
}

@keyframes orbit-reverse {
  from {
    transform: rotateZ(-360deg);
  }

  to {
    transform: rotateZ(0);
  }
}